.faqs{width:100%;max-width:900px;padding:0 25px;justify-self:stretch}.faqs .faq{margin:5px;padding:20px;color:#eeeeee;background-color:#171717;box-shadow:0px 0px 5px rgba(0,0,0,0.8);transition:all .5s ease;cursor:pointer}.faqs .faq:hover{-webkit-transform:translateY(-3px);transform:translateY(-3px);background-color:#1f1f1f;box-shadow:0px 0px 10px rgba(0,0,0,0.8)}.faqs .faq:first-child{margin-top:5px}.faqs .faq .faq-category{position:relative;left:85%;-webkit-transform:translateY(120%);transform:translateY(120%);font-size:12px}.faqs .faq .faq-questions{position:relative;font-size:20px;padding-right:80px;font-weight:400;transition:all 0.4s ease}@media (max-width: 600px){.faqs .faq .faq-questions{font-size:15px}}.faqs .faq .faq-questions::after{content:"";position:absolute;top:50%;right:0px;-webkit-transform:translateY(-50%);transform:translateY(-50%);width:30px;height:30px;background-image:url(/react-faq-page/static/media/expand_more-24px.ca15223b.svg);background-position:center;background-size:contain;background-repeat:no-repeat;transition:all .5s ease-out}.faqs .faq .faq-answer{opacity:0;max-height:0;overflow-y:hidden;font-size:16px;transition:all 0.5s ease}.faqs .faq .faq-answer--url:link,.faqs .faq .faq-answer--url:visited{text-decoration:none;display:inline-block;color:white;border-bottom:2px solid #21e6c2;transition:all 0.4s linear;margin-top:5px;font-size:14px}.faqs .faq.open{background-color:#2c2c2c;color:#eeeeee;-webkit-transform:translateY(-3px);transform:translateY(-3px);box-shadow:5px 7px 15px rgba(0,0,0,0.5)}.faqs .faq.open .faq-questions{margin-bottom:15px;color:#21e6c2;font-size:25px}.faqs .faq.open .faq-questions::after{-webkit-transform:translateY(-50%) rotate(180deg);transform:translateY(-50%) rotate(180deg);fill:white}.faqs .faq.open .faq-answer{max-height:1000px;opacity:1}.faqs .faq.open .faq-answer--url:hover{background-color:#21e6c2;box-shadow:0px 0px 10px rgba(0,0,0,0.8);-webkit-transform:translateY(-2px);transform:translateY(-2px);color:#171717