.navigation{height:350px;background-color:#171717;border-radius:2px;box-shadow:0px 0px 5px rgba(0,0,0,0.8);width:100%;max-width:200px;margin:5px 10px 0 auto;font-size:20px;justify-self:end;position:relative}@media (max-width: 600px){.navigation{font-size:15px;grid-auto-rows:row dense}}.navigation h3{margin:15px 15px;text-align:center;transition:all .5s ease;color:rgba(255,255,255,0.7);text-transform:uppercase;font-weight:500;cursor:pointer}.navigation h3:hover{color:#21e6c2;-webkit-transform:translateY(-2px);transform:translateY(-2px)}.navigation h3.active{color:#21e6c2;font-size:1.5em;-webkit-transform:translateY(-2px);transform:translateY(-2px)}@media (max-width: 600px){.navigation h3.active{font-size:1.5em}}@-webkit-keyframes sidebar{0%{opacity:0;-webkit-transform:translateX(-10px);transform:translateX(-10px)}100%{-webkit-transform:0px;transform:0px;opacity:1}}@keyframes sidebar{0%{opacity:0;-webkit-transform:translateX(-10px);transform:translateX(-10px)}100%{-webkit-transform:0px;transform:0px;opacity:1}}

.icon{font-family:'Material Icons', serif;font-style:normal;display:inline-block;vertical-align:middle;line-height:1;text-transform:none;letter-spacing:normal;word-wrap:normal;white-space:nowrap;direction:ltr;-webkit-font-smoothing:antialiased;text-rendering:optimizeLegibility;-moz-osx-font-smoothing:grayscale;-webkit-font-feature-settings:'liga';font-feature-settings:'liga'}.row{margin:0;padding:0;display:flex;flex-direction:row;flex-wrap:wrap;width:100%}.row-middle{align-items:center}.column{flex-grow:1;flex-basis:0;max-width:100%}.calendar{display:block;position:relative;width:100%;background:white;border:1px solid lightgray;height:auto;margin:0 auto}.calendar .header{text-transform:uppercase;font-weight:700;font-size:115%;padding:1.5em 0;border-bottom:1px solid lightgray}.calendar .header .icon{cursor:pointer;transition:.15s ease-out}.calendar .header .icon:hover{-webkit-transform:scale(1.75);transform:scale(1.75);transition:.25s ease-out;color:dodgerblue}.calendar .header .icon:first-of-type{margin-left:1em}.calendar .header .icon:last-of-type{margin-right:1em}.calendar .days{text-transform:uppercase;font-weight:400;color:gray;font-size:70%;padding:.75em 0;text-align:center}.calendar .body .cell{position:relative;height:30px;overflow:hidden;cursor:pointer;opacity:0.9;border-radius:50%;transition:0.25s ease-out}.calendar .body .cell:hover{background:whitesmoke;transition:0.5s ease-out}.calendar .body .cell:hover .number{opacity:0.5;transition:.3s ease-in}.calendar .body .cell:last-child{border-right:none}.calendar .body .cell .number{position:absolute;font-size:82.5%;line-height:1;top:.75em;right:.75em;font-weight:700}.calendar .body .cell .bg{font-weight:700;line-height:1;color:#e84545;opacity:0;font-size:5em;position:absolute;top:-.2em;right:-.05em;transition:.25s ease-out;letter-spacing:-.07em}.calendar .body .drsession{background-color:darkgreen;display:block;width:100%;height:100%}.calendar .body .drsession-full{background-color:grey;display:block;width:100%;height:100%}.calendar .body .selected{border-left:10px solid transparent;background:hotpink;z-index:2}.calendar .body .selected .number{visibility:hidden}.calendar .body .selected .bg{opacity:0.2;transition:.5s ease-in}.calendar .body .row{border-bottom:1px solid lightgray}.calendar .body .row:last-child{border-bottom:none}.calendar .body .disabled{color:lightgray;pointer-events:none}.calendar .body .column{flex-grow:0;flex-basis:calc(100%/7);width:calc(100%/7)}

